.dataSet {
  position: absolute;
  bottom: 20px;
  left: 20px;
  z-index: 1;
  color: #fff;
  font-size: 12px;
}

.dataSetSelect {
  display: inline-block;
}

.dataSetSelect select {
  background: transparent;
  margin-left: 5px;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px dotted #fff;
  border-radius: 0;
  font-size: 12px;
  font-weight: 900;
  color: #fff;
  cursor: pointer;
  -webkit-appearance: none;
}

.dataSetSelect select:focus {
  outline: none;
}

.tiles {
  display: grid;
  height: 100vh;
  grid-template-columns: repeat(5, 20vw);
  grid-template-rows: repeat(5, 20vh);
}

.tile {
  display: -ms-flexbox;
  display: flex;
  position: relative;
  overflow: hidden;
}

.card {
  position: absolute;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 10%;
  width: 100%;
  height: 100%;
  color: #fff;
  font-size: 24px;
  font-weight: 900;
  -webkit-transition: all 800ms cubic-bezier(0, 1, 1, 1);
  -o-transition: all 800ms cubic-bezier(0, 1, 1, 1);
  transition: all 800ms cubic-bezier(0, 1, 1, 1);
}

.text {
  -webkit-transition: opacity 800ms ease 200ms;
  -o-transition: opacity 800ms ease 200ms;
  transition: opacity 800ms ease 200ms;
}

.card:after {
  content: '|';
  color: #fff;
  font-weight: 100;
}

.card:not(.typing):after {
  -webkit-animation: 1s blink step-end infinite;
          animation: 1s blink step-end infinite;
}

.bottom-enter .text,
.top-enter .text,
.left-enter .text,
.right-enter .text {
  opacity: 0;
}

.bottom-enter.bottom-enter-active .text,
.bottom-leave .text,
.top-enter.top-enter-active .text,
.top-leave .text,
.left-enter.left-enter-active .text,
.left-leave .text,
.right-enter.right-enter-active .text,
.right-leave .text {
  opacity: 1;
}

.bottom-enter.bottom-enter-active,
.bottom-leave,
.top-enter.top-enter-active,
.top-leave,
.left-enter.left-enter-active,
.left-leave,
.right-enter.right-enter-active,
.right-leave  {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.bottom-enter,
.top-leave.top-leave-active {
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
}

.bottom-leave.bottom-leave-active,
.top-enter {
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
}

.left-enter,
.right-leave.right-leave-active {
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}

.left-leave.left-leave-active,
.right-enter {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}

@-webkit-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: #fff;
  }
}

@keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: #fff;
  }
}



